.dot-line-list {
  position: relative;
  padding-left: .27rem;
}
.dot-line-list::before {
  position: absolute;
  content: '';
  left: 0;
  top: 0;
  width: .04rem;
  height: 100%;
  background-color: rgba(255, 255, 255, 0.3);
  border-radius: .02rem;
}
.dot-line-list li {
  position: relative;
  width: 100%;
  padding: .1rem .13rem .1rem .16rem;
  box-sizing: border-box;
  margin-bottom: .27rem;
  background: #1B418D;
  box-shadow: 0 0.03rem 0.06rem rgba(0, 0, 0, 0.16);
}
.dot-line-list li .title {
  margin-bottom: .05rem;
}
.dot-line-list li .title:before {
  left: -0.25rem;
  top: 50%;
  width: .16rem;
  height: .16rem;
  border-radius: 50%;
  background-color: #5AB0F3;
  transform: translate(-50%, -50%);
}
.dot-line-list li::before {
  left: 0;
  top: 50%;
  transform: translate(-100%, -50%);
  border: .1rem solid transparent;
  border-color: transparent rgba(0, 0, 0, 0.16) transparent transparent;
}
.dot-line-list li::after {
  left: .01rem;
  top: calc(50% - 0.01rem);
  transform: translate(-100%, -50%);
  border: .1rem solid transparent;
  border-color: transparent #1B418D transparent transparent;
}
.dot-line-list li.active,
.dot-line-list li:hover {
  background-color: #2551A2;
}
.dot-line-list li.active::after,
.dot-line-list li:hover::after {
  border-color: transparent #2551A2 transparent transparent;
}
